<script>
import footerComponent from './footer.vue'
export default {
	name: 'HelloWorld',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	components:{
		footerComponent
	},
	created(){
		this.$nextTick(function () {
		
		})
	}
}
