  }
  .red{
  	color:red;
  }
  .divider{
  	width: 90%;
  	margin-left: auto;
  	margin-right: auto;
  	height: .1em;
  	background: red;
  }
  .gold{
  	color: #f6b93b;
  }
  .hello{
  	background:#1a1a1a;
  	background: linear-gradient(rgba(0,0,0,.7), rgba(26,26,26,1)),url("/static/cinema.jpg");
  	background-repeat:no-repeat;
  	background-size: cover;
  }
  .flex-direction{
  	flex-direction: row;
  }
  .flex-direction-inverted{
  	flex-direction: row;
  }
  .text{
  	max-width: 35vw;
  }
  .secondary{
  	width: 100%;
  }
  #Composant_1_1{
  	width: 18rem;
  	margin-top: 25vh;
  	height: auto;
  }
  #awards{
  	width: 18rem;
  }
  #popcorn{
  	width: 18em;
  }
  #list{
  	width: 18rem;
  }
  #Groupe_1>rect:nth-child(1){
  	animation: drink 3s ease-in infinite;
  }
  #Groupe_1>rect:nth-child(2){
  	animation: drink2 3s ease-in infinite;
  }
  #Groupe_1>rect:nth-child(3){
  	animation: drink3 3s ease-in infinite;
  }
  #Tracé_27{
  	animation: popcorn 5s infinite;	
  }
  #Tracé_83,#Tracé_81{
  	animation: oscar 1s linear alternate infinite;
  }
  #Tracé_79{
  	animation: hand 1s linear alternate infinite;
  }
  #Tracé_105{
  		stroke: red;
  		stroke-width:3px;
  		stroke-dasharray: 30%;
  		stroke-dashoffset: 30%;
  	animation: list 2s linear alternate infinite;
  }
  @keyframes list{
  	0%{
  		stroke-dashoffset: 30%;
  	}
  	60%{
  		fill: white;
  	}
  	70%{
  		fill: red;
  		stroke-dashoffset: 0%;
  	}
  	100%{
  		fill:red;
  		stroke-dashoffset: 0%;
  	}
  }
  @keyframes hand{
  	0%{
  		transform:translate(-59.5px, -23.75px);
  	}
  	50%{
  		transform: translate(-58px, -30px);
  	}
  	100%{
  		transform: translate(-59.5px, -38.75px);
  	}
  }
  @keyframes oscar{
  	0%{
  		transform:translate(-59.5px, -23.75px);
  	}
  	50%{
  		transform: translate(-58px, -30px);
  	}
  	100%{
  		transform: translate(-59.5px, -38.75px);
  	}
  }
  @keyframes popcorn{
  	0%{
  		transform:translate(-166.446px, -199.972px);
  	}
  	7%{
  		transform:translate(-166.446px, -149.972px);
  	}
  	10%{
  		transform: translate(-166.446px, -199.972px);
  	}
  	100%{
  		transform: translate(-166.446px, -199.972px);
  	}
  }
  @keyframes drink{
  	0%{
  		transform:translate(529.167px, 300.662px) rotate(90deg);
  	}
  	100%{
  		transform:translate(529.167px, 20.662px) rotate(90deg);
  	}
  }
  @keyframes drink2{
  	20%{
  		transform:translate(529.167px, 300.662px) rotate(90deg)
  	}
  	60%{
  		transform:translate(529.167px, 20.662px) rotate(90deg);
  	}
  }
  @keyframes drink3{
  	40%{
  		transform:translate(529.167px, 300.662px) rotate(90deg)
  	}
  	80%{
  		transform:translate(529.167px, 20.662px) rotate(90deg);
  	}
  }
  .main{
  	width: 100%;
  	height: auto;
  	min-height: 100vh;
  }
  .is-outlined{
  	border-color: red;
  	background: transparent;
  	color: red;
  	transition:.4s;
  }
  .is-outlined:hover{
  	background: red;
  	color: white;
  }
  #Trace_1{
  	shape-rendering: geometricPrecision;
  	stroke-width:.3px;
  	stroke-linecap: round;
  	-webkit-animation: logo 4s;
  	        animation: logo 4s;
  	stroke-dasharray: 426%;
  	stroke-dashoffset: 0%;
  	stroke: red;
  	fill: red;
  }
  @keyframes logo{
  	0%{
  		fill:transparent;
  		stroke-dasharray: 426%;
  		stroke-dashoffset: 426%;
  	}
  	60%{
  		fill: transparent;
  	}
  	100%{
  		fill:red;
  		stroke-dashoffset:0%;
  	}
  }
  	@media (max-width: 800px) {
	  .flex-direction {
	    flex-direction: column;
	  }
	  .flex-direction-inverted{
  		flex-direction: column-reverse;
  	}
	  .text{
	  	max-width: 80vw;
	  	margin-bottom: 1em;
	  }
	}
</style>



